.cube {
	position: relative;
	width: 300px;
	height: 300px;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;

	-webkit-animation: animate 15s linear infinite;

	        animation: animate 15s linear infinite;
}
@-webkit-keyframes animate {
	0% {
		-webkit-transform: rotateX(-30deg) rotateY(0);
		        transform: rotateX(-30deg) rotateY(0);
	}
	50% {
		-webkit-transform: rotateX(-90deg) rotateY(180deg);
		        transform: rotateX(-90deg) rotateY(180deg);
	}
	100% {
		-webkit-transform: rotateX(-30deg) rotateY(360deg);
		        transform: rotateX(-30deg) rotateY(360deg);
	}
}
@keyframes animate {
	0% {
		-webkit-transform: rotateX(-30deg) rotateY(0);
		        transform: rotateX(-30deg) rotateY(0);
	}
	50% {
		-webkit-transform: rotateX(-90deg) rotateY(180deg);
		        transform: rotateX(-90deg) rotateY(180deg);
	}
	100% {
		-webkit-transform: rotateX(-30deg) rotateY(360deg);
		        transform: rotateX(-30deg) rotateY(360deg);
	}
}

.cube div {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.cube div span {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(180deg, #1e1e1e 30%, #262746 100.02%);
	-webkit-transform: rotateY(calc(90deg * var(--i))) translateZ(150px);
	        transform: rotateY(calc(90deg * var(--i))) translateZ(150px);
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}
.cube div span h2 {
	position: absolute;
	font-size: 10em;
	color: white;
	-webkit-transform: translateZ(50px);
	        transform: translateZ(50px);
}
.cube div span h2:nth-child(1) {
	-webkit-transform: translateZ(0) translateY(20px);
	        transform: translateZ(0) translateY(20px);
	color: rgba(0, 0, 0, 0.1);
	-webkit-filter: blur(2px);
	        filter: blur(2px);
}
.top {
	position: absolute;
	top: 0;
	left: 0;
	width: 300px;
	height: 300px;
	background: #222;
	-webkit-transform: rotateX(90deg) translateZ(150px);
	        transform: rotateX(90deg) translateZ(150px);
}
.top::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 300px;
	height: 300px;
	background: linear-gradient(90deg, #5256fe 100.02%);
	-webkit-transform: translateZ(-380px);
	        transform: translateZ(-380px);
	-webkit-filter: blur(20px);
	        filter: blur(20px);
	box-shadow: 0 0 120px #5256fe, 0 0 200px #9352cf, 0 0 300px #ba4fb2,
		0 0 400px #e84b92, 0 0 500px #fd4982;
}
.top img {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
